<template>
	<div class="dashboard">
		<v-row class="mt-8">
			<v-col class="d-flex align-center justify-center">
				<v-card loading shaped elevation="3" class="mx-auto">
					<v-card-title class="d-flex justify-center">
						Welcome {{ username }}
					</v-card-title>
					<v-card-subtitle>
						You can wait here for a call, or type your friend's name and call
						him.
					</v-card-subtitle>
					<v-card-text>
						<v-form autocomplete="off" ref="form" @submit.prevent="call">
							<v-container>
								<v-row>
									<v-col cols="12">
										<v-text-field
											v-model="friendname"
											label="friend's username"
											:rules="rules"
											required
										></v-text-field>
										<v-text-field
											v-model="secretmessage"
											label="Enter something your friend knows ;)"
											:rules="rules"
											required
										></v-text-field>
										<p v-if="callError" class="text-center red--text">
											{{ callError }}
										</p>
									</v-col>
									<v-col cols="12" class="d-flex justify-center">
										<v-btn
											:disabled="loading"
											:loading="loading"
											type="submit"
											color="secondary"
										>
											Call
											<v-icon dark right>mdi-phone</v-icon>
										</v-btn>
									</v-col>
								</v-row>
							</v-container>
						</v-form>
					</v-card-text>
				</v-card>
			</v-col>
		</v-row>
		<!-- The calling overlay -->
		<Calling
			v-if="ringing"
			@callRejected="showCallRejected"
			@callCancel="ringing = false"
		/>
	</div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import Calling from "@/components/Calling";

export default {
	name: "Dashboard",
	components: {
		Calling
	},
	created() {
		setInterval(() => {
			this.$socket.emit("check-connection", {
				username: this.username
			});
		}, 1000);

		this.$socket.on("check-connection", data => {
			if (!data.registered) {
				this.$router.push("/");
			}
		});
	},
	destroyed() {
		this.$socket.off("check-connection");
	},
	data() {
		return {
			rules: [value => !!value || "Required."],
			loading: false,
			friendname: null,
			secretmessage: null,
			ringing: false,
			callError: null
		};
	},
	methods: {
		...mapMutations(["setFriendname", "setSecretmessage"]),

		call() {
			this.callError = null;
			this.loading = true;
			let validationResult = this.$refs.form.validate();
			if (validationResult) {
				if (this.username == this.friendname) {
					this.callError = "You can't call yourself";
					this.loading = false;
					return;
				}
				this.setFriendname(this.friendname);
				this.setSecretmessage(this.secretmessage);
				/* make the call */
				this.$socket.emit("call-init", {
					username: this.username,
					friendname: this.friendname,
					secretmessage: this.secretmessage
				});

				this.$socket.on("call-init-success", () => {
					/* On successful call setup, show the ringing overlay. */
					this.loading = false;
					this.ringing = true;
				});

				this.$socket.on("call-init-error", data => {
					this.loading = false;
					this.callError = data.error;
				});
			} else {
				this.loading = false;
			}
		},
		showCallRejected() {
			this.ringing = false;
			this.callError = "Sorry, user is busy now.";
		}
	},
	computed: {
		...mapState(["username"])
	}
};
</script>

<style scoped></style>
