<template>
	<div class="incoming">
		<v-overlay absolute opacity="0.9" z-index="999">
			<v-row>
				<v-col cols="12" class="d-flex justify-center">
					<h2>Incoming Call from {{ friendname }}</h2>
				</v-col>
				<v-col cols="12" class="d-flex justify-center">
					<p>{{ incomingsecret }}</p>
				</v-col>
				<v-col class="d-flex justify-space-around">
					<v-btn @click="answerCall" color="secondary">Answer</v-btn>
					<v-btn @click="rejectCall">Reject</v-btn>
				</v-col>
			</v-row>
		</v-overlay>
	</div>
</template>

<script>
import ringtone from "@/assets/sounds/iphone-ringtone.mp3";
import { mapState, mapMutations } from "vuex";
export default {
	name: "Incoming",
	data() {
		return {
			ringToneAudio: null
		};
	},
	computed: {
		...mapState(["friendname", "username", "incomingsecret"])
	},
	methods: {
		...mapMutations(["setInitiator", "setInCall"]),
		playRingtone() {
			this.ringToneAudio = new Audio(ringtone);
			this.ringToneAudio.play();
		},
		stopRingtone() {
			this.ringToneAudio.pause();
			this.ringToneAudio.currentTime = 0;
		},
		rejectCall() {
			this.$socket.emit("call-reject", {
				username: this.username,
				friendname: this.friendname
			});
			this.stopRingtone();
			this.setInCall(false);
			this.$emit("callReject");
		},
		answerCall() {
			this.$socket.emit("call-accept", {
				username: this.username,
				friendname: this.friendname
			});
			this.setInitiator(false);
			this.stopRingtone();
			this.$emit("closeOverlay");
			this.$router.push("call");
		}
	},
	created() {
		this.setInCall(true);
		this.playRingtone();
	}
};
</script>

<style scoped></style>
