<template>
	<v-app>
		<v-app-bar elevation="0" app color="primary" dark>
			<v-row>
				<v-col class="d-flex justify-center">
					<v-toolbar-title>Simple voice call App</v-toolbar-title>
				</v-col>
			</v-row>
		</v-app-bar>

		<v-content>
			<v-container fluid>
				<router-view></router-view>
			</v-container>
		</v-content>

		<v-footer app color="primary" dark>
			<v-card-text class="d-flex justify-center">
				{{ new Date().getFullYear() }} — <strong>Simple Voice Call App</strong>
			</v-card-text>
		</v-footer>

		<Incoming
			@closeOverlay="closeOverlay"
			@callReject="closeOverlay"
			v-if="incomingCall"
		/>
	</v-app>
</template>

<script>
import Incoming from "@/components/Incoming";
import { mapState, mapMutations } from "vuex";

export default {
	name: "App",
	components: {
		Incoming
	},
	data: () => ({
		incomingCall: false
	}),
	created() {
		this.$socket.on("incoming-call", data => {
			if (!this.inCall) {
				this.setFriendname(data.username);
				this.setIncomingSecret(data.secretmessage);
				this.incomingCall = true;
			} else {
				this.$socket.emit("call-reject", {
					username: this.username,
					friendname: data.username
				});
			}
		});
	},
	methods: {
		...mapMutations(["setFriendname", "setIncomingSecret"]),
		closeOverlay() {
			this.incomingCall = false;
		}
	},
	computed: {
		...mapState(["inCall", "username"])
	}
};
</script>

<style>
/* For the theme background color */
.v-application {
	background-color: var(--v-background-base) !important;
}
</style>
