import adapter from "webrtc-adapter"; // eslint-disable-line no-unused-vars

class WebRTCService {
	mediaConstraints = {
		audio: {
			sampleRate: 8000,
			sampleSize: 8,
			volume: 1,
			echoCancellation: true,
			noiseSuppression: true
		}
	};

	iceServers = {};

	peerConnection = null;

	localStream = null;

	constructor(socket, audio, username, friendname) {
		this.$socket = socket;
		this.username = username;
		this.friendname = friendname;
		this.audio = audio;
	}

	createPeerConnection() {
		this.peerConnection = new RTCPeerConnection(this.iceServers);

		this.peerConnection.onicecandidate = this.handleIceCandidateEvent.bind(
			this
		);
		this.peerConnection.ontrack = this.handleTrackEvent.bind(this);
		this.peerConnection.onnegotiationneeded = this.handleNegotiationNeededEvent.bind(
			this
		);
		this.peerConnection.oniceconnectionstatechange = this.handleICEConnectionStateChangeEvent.bind(
			this
		);
		this.peerConnection.onsignalingstatechange = this.handleSignalingStateChangeEvent.bind(
			this
		);
	}

	async getMediaStream() {
		try {
			this.localStream = await navigator.mediaDevices
				.getUserMedia(this.mediaConstraints)
				.catch(this.handleGetUserMediaError);
			this.localStream.getTracks().forEach(track => {
				this.peerConnection.addTransceiver(track, {
					streams: [this.localStream]
				});
			});
		} catch (e) {
			this.handleGetUserMediaError(e);
		}
	}

	closeCall() {
		this.peerConnection.ontrack = null;
		this.peerConnection.onicecandidate = null;
		this.peerConnection.oniceconnectionstatechange = null;
		this.peerConnection.onsignalingstatechange = null;
		this.peerConnection.onicegatheringstatechange = null;
		this.peerConnection.onnegotiationneeded = null;
		if (this.audio.srcObject) {
			this.audio.srcObject.getTracks().forEach(track => track.stop());
		}
		this.peerConnection.close();
		this.peerConnection = null;
	}

	handleGetUserMediaError(e) {
		switch (e.name) {
			case "NotFoundError":
				alert(
					"Unable to open your call because no camera and/or microphone" +
						"were found."
				);
				break;
			case "SecurityError":
			case "PermissionDeniedError":
				// Do nothing; this is the same as the user canceling the call.
				break;
			default:
				alert("Error opening your camera and/or microphone: " + e.message);
				break;
		}

		this.closeCall();
	}

	async handleNegotiationNeededEvent() {
		try {
			let offer = await this.peerConnection.createOffer();

			if (this.peerConnection.signalingState != "stable") {
				return;
			}

			await this.peerConnection.setLocalDescription(offer);
			this.$socket.emit("peer-data", {
				username: this.username,
				friendname: this.friendname,
				type: "offer",
				sdp: this.peerConnection.localDescription
			});
		} catch (e) {
			// console.log(e);
		}
	}

	async handleOfferMsg(msg) {
		if (!this.peerConnection) {
			this.createPeerConnection();
		}

		const offer = new RTCSessionDescription(msg.sdp);

		if (this.peerConnection.signalingState != "stable") {
			await Promise.all([
				this.peerConnection.setLocalDescription({ type: "rollback" }),
				this.peerConnection.setRemoteDescription(offer)
			]);
			return;
		} else {
			await this.peerConnection.setRemoteDescription(offer);
		}

		if (!this.localStream) {
			await this.getMediaStream().catch(this.handleGetUserMediaError);
		}

		await this.peerConnection.setLocalDescription(
			await this.peerConnection.createAnswer()
		);

		this.$socket.emit("peer-data", {
			username: this.username,
			friendname: this.friendname,
			type: "answer",
			sdp: this.peerConnection.localDescription
		});
	}

	handleIceCandidateEvent(e) {
		if (e.candidate) {
			this.$socket.emit("peer-data", {
				username: this.username,
				friendname: this.friendname,
				type: "new-ice-candidate",
				candidate: e.candidate
			});
		}
	}

	async handleAnswerMsg(msg) {
		const answer = new RTCSessionDescription(msg.sdp);
		await this.peerConnection.setRemoteDescription(answer).catch(() => {
			// console.log(e);
		});
	}

	async handleNewIceCandidateMsg(msg) {
		const candidate = new RTCIceCandidate(msg.candidate);

		try {
			await this.peerConnection.addIceCandidate(candidate);
		} catch (e) {
			// console.log(e);
		}
	}

	handleTrackEvent(e) {
		this.audio.srcObject = e.streams[0];
	}

	handleICEConnectionStateChangeEvent() {
		switch (this.peerConnection.iceConnectionState) {
			case "closed":
			case "failed":
			case "disconnected":
				this.closeCall();
				break;
		}
	}

	handleSignalingStateChangeEvent() {
		switch (this.peerConnection.signalingState) {
			case "closed":
				this.closeCall();
				break;
		}
	}

	mute(isMute) {
		if (isMute) {
			this.localStream.getAudioTracks()[0].enabled = false;
		} else {
			this.localStream.getAudioTracks()[0].enabled = true;
		}
	}
}

export default WebRTCService;
