import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

/* Base styles */
import "@/assets/css/base.css";

import vuetify from "./plugins/vuetify";
import sockets from "./plugins/sockets";
Vue.use(sockets);

Vue.config.productionTip = false;

new Vue({
	router,
	store,
	vuetify,
	render: h => h(App)
}).$mount("#app");
