<template>
	<div class="calling">
		<v-overlay absolute opacity="0.9" z-index="999">
			<v-row>
				<v-col cols="12" class="d-flex justify-center">
					<h2>Calling {{ friendname }}</h2>
				</v-col>
				<v-col class="d-flex justify-space-around">
					<v-btn @click="cancelCall()">Cancel Call</v-btn>
				</v-col>
			</v-row>
		</v-overlay>
	</div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import calltone from "@/assets/sounds/tele_ring.mp3";
export default {
	name: "Calling",
	data() {
		return {
			callToneAudio: null
		};
	},
	methods: {
		...mapMutations(["setInCall", "setInitiator"]),
		playRingtone() {
			this.callToneAudio = new Audio(calltone);
			this.callToneAudio.play();
			this.callToneAudio.loop = true;
		},
		stopRingtone() {
			this.callToneAudio.pause();
			this.callToneAudio.currentTime = 0;
		},
		cancelCall() {
			//Do some stuff...
			this.stopRingtone();
			this.setInCall(false);
			this.$emit("callCancel");
		}
	},
	computed: {
		...mapState(["friendname"])
	},
	created() {
		/* Set the In Call mutation. */
		this.setInCall(true);

		this.playRingtone();

		/* Wait for the user reply. Once he replies */
		this.$socket.on("call-accepted", () => {
			this.stopRingtone();
			this.setInitiator(true);
			this.$router.push("call");
		});

		this.$socket.on("call-rejected", () => {
			this.stopRingtone();
			this.setInCall(false);
			this.$emit("callRejected");
		});
	},
	destroyed() {
		this.$socket.off("call-accepted");
		this.$socket.off("call-rejected");
	}
};
</script>

<style scoped></style>
